#addonify-compare-dock-compare-btn {
  color: var(--adfy_compare_products_dock_compare_button_color);
  fill: var(--adfy_compare_products_dock_compare_button_color);
  background: transparent;
  border: none;
  box-shadow: none;
  cursor: pointer;
  background-color: var(--adfy_compare_products_dock_compare_button_bg_color);
  transition: all 0.5s ease;
}
#addonify-compare-dock-compare-btn:hover {
  color: var(--adfy_compare_products_dock_compare_button_color_hover);
  fill: var(--adfy_compare_products_dock_compare_button_color_hover);
  background-color: var(--adfy_compare_products_dock_compare_button_bg_color_hover);
