  text-overflow: ellipsis;
}
#addonify-compare-search-results .item-add {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  font-weight: normal;
  line-height: 1;
  text-align: center;
  color: var(--adfy_compare_products_search_modal_add_button_color);
  fill: var(--adfy_compare_products_search_modal_add_button_color);
  background: transparent;
  background-color: var(--adfy_compare_products_search_modal_add_button_bg_color);
  cursor: pointer;
  border: none;
  box-shadow: none;
  border-radius: 3px;
  transition: all 0.5s ease;
}
#addonify-compare-search-results .item-add svg {
