  height: 13px;
}
#addonify-compare-search-results .item-add:hover {
  color: var(--adfy_compare_products_search_modal_add_button_color_hover);
  fill: var(--adfy_compare_products_search_modal_add_button_color_hover);
  background: transparent;
  background-color: var(--adfy_compare_products_search_modal_add_button_bg_color_hover);
  box-shadow: none;
  border: none;
}

/* -------------------------------------------------------------------------- 
	Compare modal box [Compare table]
  -------------------------------------------------------------------------- */
#addonify-compare-modal {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: 20px;
  width: auto;
  height: auto;
  z-index: 999999999;
  overflow: hidden;
  border-radius: 5px;
  border-top-right-radius: 0;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}
#addonify-compare-modal .addonify-compare-model-inner {
  position: relative;
  max-width: 100%;
}
#addonify-compare-modal .addonify-compare-model-inner #addonify-compare-close-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: auto;
  right: 0;
  top: 0;
  height: 30px;
  width: 30px;
  padding: 0;
  margin: 0;
  border-radius: 0;
  border-bottom-left-radius: 3px;
  box-shadow: none;
  z-index: 10;
}
#addonify-compare-modal .addonify-compare-model-inner #addonify-compare-close-button svg {
  width: 14px;
  height: 14px;
}
#addonify-compare-modal .addonify-compare-model-inner #addonify-compare-modal-content {
  position: relative;
  display: block;
  margin: 0 auto;
  width: 100%;
  padding-bottom: 30px;
  height: calc(100vh - 20px);
  background-color: white;
  border-radius: 0;
  overflow: hidden;
  overflow-y: auto;
}

/* -------------------------------------------------------------------------- 
	Compare page
  -------------------------------------------------------------------------- */
#addonify-compare-products-table {
  display: table;
  table-layout: auto;
  border-collapse: collapse;
  margin: 0;
  padding: 0;
  overflow: auto;
}
#addonify-compare-products-table th,
#addonify-compare-products-table td {
  padding: 15px 15px;
  max-width: 400px;
  min-width: 200px;
  vertical-align: middle;
  text-align: left;
  border-color: #F5F5F5;
}
#addonify-compare-products-table th {
  background-color: #F5F5F5;
}
#addonify-compare-products-table td .price {
  display: inline-flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  font-size: 16px;
  color: #444444;
  font-weight: bold;
  margin-bottom: 0;
}
#addonify-compare-products-table td .price ins {
  display: inline-flex;
  font-weight: bold;
  color: red;
  background-color: transparent;
}
#addonify-compare-products-table td .price del {
  display: inline-flex;
  margin-right: 5px;
  font-size: 13px;
  color: #444444;
  opacity: 0.8;
}
#addonify-compare-products-table td .star-rating {
  float: left;
}
#addonify-compare-products-table td .add_to_cart_inline {
  margin-bottom: 0;
}
#addonify-compare-products-table tbody tr td:first-child {
  background-color: #F5F5F5;
}

/* -------------------------------------------------------------------------- 
	Loading wave
  -------------------------------------------------------------------------- */
@keyframes addonifyCompareLoadingWaveAfter {
  from {
    transform: scale(0.5, 0.5);
    opacity: 0;
  }
  to {
    transform: scale(1, 1);
    opacity: 1;
  }
}
@keyframes addonifyCompareLoadingWaveBefore {
  from {
    -webkit-transform: scale(1, 1);
    opacity: 1;
  }
  to {
    -webkit-transform: scale(1.5, 1.5);
    opacity: 0;
  }
}